<template>
  <div class="pool-wrapper">
    <Form inline>
      <FormItem>
        <Select v-model="formData.cityCode" placeholder="选择城市" @on-change="cityChange">
          <Option v-for="(option,index) in cityList" :value="option.id" :key="index">{{option.name}}</Option>
        </Select>
      </FormItem>
      <FormItem>
        <DatePicker v-model="timeRange" @on-change="timeChange"  type="datetimerange" placeholder="流入时间" style="width:310px"></DatePicker>
      </FormItem>
      <FormItem>
        <Select v-model="formData.stuGradeClub" placeholder="选择年部" clearable @on-change="gtChange">
          <Option v-for="(option,index) in gradeTypeList" :value="option.id" :key="index">{{option.name}}</Option>
        </Select>
      </FormItem>
      <FormItem>
        <Select v-model="formData.stuGrade" placeholder="选择年级" clearable>
          <Option v-for="(option,index) in gradeList" :value="option.id" :key="index">{{option.name}}</Option>
        </Select>
      </FormItem>
      <FormItem>
        <Select v-model="formData.stuFollowEmp" placeholder="维护顾问" clearable filterable>
          <Option v-for="(option,index) in advisorList" :value="option.empNo" :key="index">{{option.empName}}</Option>
        </Select>
      </FormItem>
      <FormItem>
        <Input v-model="formData.stuName" placeholder="用户名" clearable></Input>
      </FormItem>
      <FormItem>
        <Input v-model="formData.phone" type="number" :maxlength="11" placeholder="手机号" clearable></Input>
      </FormItem>
      <FormItem>
        <Button type="primary" @click="search">查询</Button>
      </FormItem>
    </Form>
    <div style="text-align: right;margin-bottom: 10px">
      <Button :disabled="!checkAllGroup.length" @click="openModal(1)" type="primary">选中分配</Button>
      <Button @click="openModal(2)" type="primary">分配记录</Button>
    </div>
    <div class="ivu-table ivu-table-default ivu-table-border" style="height:auto">
      <Spin size="large" fix v-if="tableLoading"></Spin>
      <div class="ivu-table-body ivu-table-overflowX">
        <CheckboxGroup
          v-model="checkAllGroup"
          style="font-size: 12px"
          @on-change="checkCheckboxStatus">
          <table cellspacing="0" cellpadding="0" border="0" style="width: 100%">
            <colgroup>
              <col style="width: 50px"></col>
              <col style="width: 70px"></col>
              <col></col>
              <col style="width: 160px"></col>
              <col style="width: 150px"></col>
              <col></col>
              <col></col>
              <col></col>
              <col></col>
              <col></col>
              <col></col>
              <col style="width: 165px"></col>
              <col></col>
            </colgroup>
            <thead>
              <tr class="ivu-table-row">
                <th class="ivu-table-column-center" rowspan="2">
                  <label class="tal-ipt-checkbox" style="margin-left: 0">
                    <input type="checkbox" class="ivu-checkbox-inner" style="margin-left: -9px;" @click="checkAllHandler" :checked="checkAll" />
                    <span></span>
                  </label>
                </th>
                <th class="ivu-table-column-center" rowspan="2"><div class="ivu-table-cell">操作</div></th>
                <th class="ivu-table-column-center" colspan="5"><div class="ivu-table-cell">注册必填信息</div></th>
                <th class="ivu-table-column-center"><div class="ivu-table-cell">维护信息</div></th>
                <th class="ivu-table-column-center" colspan="2"><div class="ivu-table-cell">商品信息</div></th>
                <th class="ivu-table-column-center" colspan="3"><div class="ivu-table-cell">订单信息</div></th>
              </tr>
              <tr class="ivu-table-row">
                <th class="ivu-table-column-center"><div class="ivu-table-cell">姓名</div></th>
                <th class="ivu-table-column-center"><div class="ivu-table-cell">手机号</div></th>
                <th class="ivu-table-column-center"><div class="ivu-table-cell">城市</div></th>
                <th class="ivu-table-column-center"><div class="ivu-table-cell">年部</div></th>
                <th class="ivu-table-column-center"><div class="ivu-table-cell">年级</div></th>
                <th class="ivu-table-column-center"><div class="ivu-table-cell">维护顾问</div></th>
                <th class="ivu-table-column-center"><div class="ivu-table-cell">商品名称</div></th>
                <th class="ivu-table-column-center"><div class="ivu-table-cell">实付金额</div></th>
                <th class="ivu-table-column-center"><div class="ivu-table-cell">来源</div></th>
                <th class="ivu-table-column-center" width="300"><div class="ivu-table-cell">下单时间</div></th>
                <th class="ivu-table-column-center"><div class="ivu-table-cell">订单编号</div></th>
              </tr>
            </thead>
            <tbody class="ivu-table-tbody">
            <template v-for="(item,index1) in tableData">
              <template v-if="item.orderEntity && item.orderEntity.length" v-for="(item2,index2) in item.orderEntity">
                <template v-if="item2.goodList && item2.goodList.length">
                  <tr class="ivu-table-row" v-for="(item3,index3) in item2.goodList" :key="item3.id">
                  <template v-if="index2 === 0 && index3 === 0">
                    <td class="ivu-table-column-center" :rowspan="item.rowspan">
                      <div class="ivu-table-cell">
                        <Checkbox :label="item.uphone"  style="margin-right: 0">&nbsp;</Checkbox>
                      </div>
                    </td>
                    <td class="ivu-table-column-center" :rowspan="item.rowspan">
                      <div class="ivu-table-cell">
                        <Button v-if="item.edit" size="small" @click="saveRow(index1)" type="primary">保存</Button>
                        <Button v-else size="small" type="primary" @click="editRow(index1)">编辑</Button>
                      </div>
                    </td>
                    <td class="ivu-table-column-center" :rowspan="item.rowspan">
                      <div class="ivu-table-cell">
                        <Input v-if="item.edit" v-model="item.uname"></Input>
                        <span v-else>{{item.uname}}</span>
                      </div>
                    </td>
                    <td class="ivu-table-column-center" :rowspan="item.rowspan">
                      <div class="ivu-table-cell">
                        <Input v-if="item.edit" v-model="item.uphone" type="number" :maxlength="11"></Input>
                        <div v-else>
                          <span>{{ item.uphone }}</span>
                          <Icon style="cursor: pointer" v-if="!/^\d{11}$/.test(item.uphone)" @click="decodePhone(index1)" type="md-eye" />
                          <Icon custom="iconfont icon-phone-fill" style="font-size: 16px;cursor: pointer" @click="call(index1)"></Icon>
                        </div>
                      </div>
                    </td>
                    <td class="ivu-table-column-center" :rowspan="item.rowspan">
                      <div class="ivu-table-cell">
                        <Cascader v-if="item.edit" :data="cityCascade" v-model="item.cascade" placeholder="选择城市" @on-change="editCityChange($event, index1)"></Cascader>
<!--                        <Select v-if="item.edit" v-model="item.ucity" placeholder="选择城市" @on-change="editCityChange(index1)">-->
<!--                          <Option v-for="(option,index) in cityList" :value="option.id" :key="index">{{option.name}}</Option>-->
<!--                        </Select>-->
                        <span v-else>{{ item.regProvinceName }}-{{ item.regCityName }}</span>
                      </div>
                    </td>
                    <td class="ivu-table-column-center" :rowspan="item.rowspan">
                      <div class="ivu-table-cell">
                        <Select v-if="item.edit" v-model="item.ugradeClub" @on-change="editGtChange(index1)" clearable>
                          <Option v-for="(option,index) in editGradeTypeList" :value="Number(option.schoolId)" :key="index">{{option.name}}</Option>
                        </Select>
                        <span v-else>{{ item.ugradeClubName }}</span>
                      </div>
                    </td>
                    <td class="ivu-table-column-center" :rowspan="item.rowspan">
                      <div class="ivu-table-cell">
                        <Select v-if="item.edit" v-model="item.ugrade" clearable>
                          <Option v-for="(option,index) in editGradeList" :value="Number(option.schoolId)" :key="index">{{option.name}}</Option>
                        </Select>
                        <span v-else>{{ item.ugradeName }}</span>
                      </div>
                    </td>
                    <td class="ivu-table-column-center" :rowspan="item.rowspan"><div class="ivu-table-cell">{{ item.userName }}</div></td>
                  </template>
                  <td class="ivu-table-column-center"><div class="ivu-table-cell">{{item3.title}}</div></td>
                  <td class="ivu-table-column-center"><div class="ivu-table-cell">{{item3.totalFee}}</div></td>
                  <template v-if="index3 === 0">
                    <td class="ivu-table-column-center" :rowspan="item2.rowspan"><div class="ivu-table-cell">{{item2.sourceName}}</div></td>
                    <td class="ivu-table-column-center" :rowspan="item2.rowspan"><div class="ivu-table-cell">{{item2.orderCreated}}</div></td>
                    <td class="ivu-table-column-center" :rowspan="item2.rowspan"><div class="ivu-table-cell">{{item2.orderId}}</div></td>
                  </template>
                </tr>
                </template>
              </template>
            </template>
            </tbody>
          </table>
        </CheckboxGroup>
        <div v-show="!tableData.length" style="text-align: center;line-height: 50px">
            <span>暂无数据</span>
        </div>
      </div>
    </div>
    <div style="text-align: right;margin-top: 10px;">
      <Page
        :total="pageTotal"
        :page-size="formData.pageSize"
        :current="formData.pageNo"
        show-elevator
        show-sizer
        show-total
        @on-page-size-change="pageSizeChange"
        @on-change="changePage"
      ></Page>
    </div>
    <DistributeModal ref="DstributeModalRef" @close="handleModalClose" :pyUids="checkAllGroup" @success="search"/>
  </div>
</template>

<script>
import { getCityList, getLinkage, getProvinceListByType, isOpenCity, getGradeType, queryGradeByGradeType } from '@/api/bd'
import { getClueList, getClueCityAdvisor, deCrypt, updateStudentBaseInfo, addClueStudent } from '@/api/clue'
import DistributeModal from '@/components/distribute/distribute-modal'
export default {
  name: 'CluePool',
  props: {
    searchType: Number
  },
  components: {
    DistributeModal
  },
  data () {
    return {
      cityList: [],
      cityCascade: [],
      gradeTypeList: [],
      editGradeTypeList: [],
      gradeList: [],
      editGradeList: [],
      advisorList: [],
      timeRange: [],
      formData: {
        cityCode: '',
        searchType: '',
        identityType: '',
        identityValue: '',
        ctimeStart: '',
        ctimeEnd: '',
        stuGradeClub: '',
        stuGrade: '',
        stuFollowEmp: '',
        stuName: '',
        phone: '',
        pageNum: 1,
        pageSize: 10
      },
      tableData: [],
      pageTotal: 0,
      checkAll: false,
      checkAllGroup: [],
      tableLoading: false,
      isOpenCity: false
    }
  },
  computed: {
    userInfo () {
      return this.$store.state.roleRelativeData.userInfo
    },
    identityValue () {
      switch (this.userInfo.crmRoleId) {
        case 2:
          return this.formData.cityCode
        case 4:
          return this.userInfo.crmDeptId
        case 5:
          return this.userInfo.identityId
        default:
          return null
      }
    }
  },
  methods: {
    async decodePhone (index) {
      const decodePhone = await deCrypt({
        phone: this.tableData[index].uphone
      })
      this.$set(this.tableData[index], 'uphone', decodePhone)
    },
    // 没配关闭之后
    handleModalClose () {
      this.checkAllGroup = []
    },
    async call (index) {
      const callPhone = await deCrypt({
        phone: this.tableData[index].uphone
      })
      this.$store.state.app.callInstance.callByPhone(callPhone)
    },
    openModal (index) {
      this.$refs.DstributeModalRef.open(index)
    },
    search () {
      this.pageNo = 1
      this.getTableData()
    },
    cityChange () {
      this.formData.stuGradeClub = ''
      this.formData.stuGrade = ''
      this.formData.stuFollowEmp = ''
      this._getGradeTypeList()
      this._getClueCityAdvisor()
    },
    gtChange () {
      this.formData.stuGrade = ''
      this._getGradeList()
    },
    timeChange () {
      this.formData.ctimeStart = this.timeRange[0] ? this.timeRange[0].getTime() / 1000 : ''
      this.formData.ctimeEnd = this.timeRange[1] ? this.timeRange[1].getTime() / 1000 : ''
    },
    async editCityChange (data, index) {
      this.editGradeTypeList = []
      this.editGradeList = []
      this.$set(this.tableData[index], 'ugradeClub', '')
      this.$set(this.tableData[index], 'ugrade', '')
      this._getEditGradeTypeList(data[1])
    },
    editGtChange (index) {
      this.editGradeList = []
      this.$set(this.tableData[index], 'ugrade', '')
      this._getEditGradeList(index)
    },
    saveRow (index) {
      const row = this.tableData[index]
      if (!row.uphone) {
        this.$Message.error('手机号码不能为空')
        return
      }
      let regProvinceName = ''
      let regCityName = ''
      const province = this.cityCascade.find(c => c.value === row.cascade[0])
      if (province) {
        regProvinceName = province.label
        const city = province.children.find(c => c.value === row.cascade[1])
        if (city) {
          regCityName = city.label
        }
      }
      updateStudentBaseInfo({
        cityCode: row.ucity,
        id: row.id,
        phone: row.uphone,
        regProvince: row.cascade[0],
        regProvinceName,
        regCity: row.cascade[1],
        regCityName,
        stuCity: row.ucity,
        stuGrade: row.ugrade,
        stuGradeName: row.ugrade ? this.editGradeList.find(g => Number(g.schoolId) === row.ugrade).name : '',
        stuGradeClub: row.ugradeClub,
        stuGradeClubName: row.ugradeClub ? this.editGradeTypeList.find(g => Number(g.schoolId) === row.ugradeClub).name : '',
        stuName: row.uname
      }).then(() => {
        if (row.uname && row.ugradeClub && row.ugrade && row.uphone && row.cascade[0] && row.cascade[1]) { // 全部补全才能触发注册提醒
          this.$Modal.confirm({
            title: '注册提醒',
            content: '注册必填信息已补全，确认信息无误请点击"注册"，如需修改信息请点击"返回"',
            okText: '注册',
            cancelText: '返回',
            onOk: () => {
              let cityId = ''
              const province = this.cityCascade.find(c => c.value === row.cascade[0])
              if (province) {
                const city = province.children.find(c => c.value === row.cascade[1])
                if (city) {
                  cityId = city.cityId
                }
              }
              this._addClueStudent({
                provinceId: row.cascade[0],
                cityCode: row.cascade[1],
                cityId,
                userName: row.uname,
                gradeTypeId: row.ugradeClub,
                gradeId: row.ugrade,
                phone: row.uphone,
                orderIds: row.orderEntity.map(o => o.orderId).join(',')
              })
            },
            onCancel: () => {
              this.getTableData()
            }
          })
        } else {
          this.getTableData()
          this.$Message.success('保存成功')
        }
      }).catch(err => {
        this.$Message.error('保存失败：' + err.message)
      })
    },
    _addClueStudent (params) {
      addClueStudent(params).then(() => {
        this.$Message.success('注册成功')
        this.getTableData()
      }).catch(err => {
        this.$Message.error('注册失败：' + err.message)
      })
    },
    async editRow (index) {
      const isEdit = this.tableData.filter(t => t.edit).length > 0
      if (isEdit) {
        this.$Message.warning('请先提交已编辑学员')
        return
      }
      const dphone = await deCrypt({
        phone: this.tableData[index].uphone
      })
      await this._getEditGradeTypeList(this.tableData[index].cascade[1])
      await this._getEditGradeList(index)
      this.$set(this.tableData[index], 'uphone', dphone)
      this.$set(this.tableData[index], 'edit', true)
    },
    getTableData () {
      if (!this.userInfo.crmRoleId || !this.identityValue) {
        this.$Message.error('角色数据错误，请联系管理员')
        return
      }
      this.checkAll = false
      this.allItem = []
      this.tableLoading = true
      this.checkAllGroup = []
      this.formData.searchType = this.searchType
      this.formData.identityType = this.searchType === 2 ? 2 : this.userInfo.crmRoleId
      this.formData.identityValue = this.searchType === 2 ? this.formData.cityCode : this.identityValue
      getClueList(this.formData).then(data => {
        this.tableData = data.list && data.list.length ? data.list.map(l => {
          l.cascade = [ l.regProvince, l.regCity ]
          l.orderEntity = l.orderEntity.map(o => {
            o.rowspan = o.goodList.length
            return o
          })
          l.rowspan = l.orderEntity && l.orderEntity.length ? l.orderEntity.reduce((le, ri) => le + ri.rowspan, 0) : 0
          return l
        }) : []
        this.allItem = this.tableData.map(t => t.uphone)
        this.pageTotal = data.total
      }).catch(err => {
        this.$Message.error('查询线索池列表错误：' + err.message)
      }).finally(() => {
        this.tableLoading = false
      })
    },
    pageSizeChange (size) {
      this.formData.pageNum = 1
      this.formData.pageSize = size
      this.getTableData()
    },
    changePage (num) {
      this.formData.pageNum = num
      this.getTableData()
    },
    checkCheckboxStatus () {
      if (this.checkAllGroup.length === this.allItem.length) {
        this.checkAll = true
      } else {
        this.checkAll = false
      }
    },
    checkAllHandler () {
      this.checkAll = !this.checkAll
      if (!this.checkAll) {
        this.checkAllGroup = []
      } else {
        this.checkAllGroup = this.allItem
      }
    },
    async _getCityList () {
      try {
        this.cityList = await getCityList()
        this.formData.cityCode = this.cityList[0].id
      } catch (err) {
        this.$Message.error('查询城市列表错误：' + err.message)
      }
    },
    async _getGradeTypeList () {
      try {
        this.gradeTypeList = await getLinkage({
          cityCode: this.formData.cityCode,
          queryType: 'gt'
        })
      } catch (err) {
        this.$Message.error('查询年部列表错误：' + err.message)
      }
    },
    async _getGradeList () {
      try {
        this.gradeList = await getLinkage({
          cityCode: this.formData.cityCode,
          gtIds: this.formData.stuGradeClub,
          queryType: 'g'
        })
      } catch (err) {
        this.$Message.error('查询年级列表错误：' + err.message)
      }
    },
    async _getEditGradeTypeList (cityCode) {
      try {
        if (cityCode) {
          this.isOpenCity = await isOpenCity({
            areaCode: cityCode,
            schoolCode: cityCode
          })
          let _code = this.isOpenCity ? cityCode : '1111'
          this.editGradeTypeList = await getGradeType({ cityCode: _code, schoolCode: _code })
          this.editGradeTypeList = this.editGradeTypeList.filter(gt => gt.name !== '非受限年部')
        }
      } catch (err) {
        this.$Message.error('编辑-查询年部列表错误：' + err.message)
      }
    },
    async _getEditGradeList (index) {
      try {
        const row = this.tableData[index]
        if (row.cascade[1] && row.ugradeClub) {
          this.editGradeList = await queryGradeByGradeType({
            gradeTypeId: row.ugradeClub,
            cityCode: this.isOpenCity ? row.cascade[1] : '1111'
          })
        }
      } catch (err) {
        this.$Message.error('编辑-查询年级列表错误：' + err.message)
      }
    },
    async _getClueCityAdvisor () {
      try {
        this.advisorList = await getClueCityAdvisor({
          cityCode: this.formData.cityCode
        })
      } catch (err) {
        this.$Message.error('查询顾问列表错误：' + err.message)
      }
    },
    async _getProvinceListByType () {
      try {
        const data = await getProvinceListByType({ type: 'c' })
        this.cityCascade = data.map(d => {
          const cityList = d.cityDtoList.map(c => {
            return { value: c.areaCode, label: c.name, cityId: c.id }
          })
          return {
            value: d.schoolId,
            label: d.name,
            children: cityList
          }
        })
      } catch (err) {
        this.$Message.error('查询省市选项错误：' + err.message)
      }
    }
  },
  async mounted () {
    await this._getProvinceListByType()
    await this._getCityList()
    await this._getClueCityAdvisor()
    this._getGradeTypeList()
    this._getGradeList()
    this.getTableData()
  }
}
</script>

<style scoped>
  .pool-wrapper {
    margin: 10px;
    padding: 10px;
    background: #FFFFFF;
  }
</style>
