  overflow-y: scroll;
  .dist_detail {
    padding-bottom: 25px;
    .table_header {
      span.first_label,
      span.last_label {
        font-size: 12px;
        line-height: 22px;
        margin-right: 16px;
        margin-top: 5px;
      }
      .detail_form {
        display: flex;
        border-bottom: 1px solid #f0f0f0;
        padding: 0px 0 25px 0;
        margin-bottom: 25px;
