<template>
  <div class="layout">
    <h3 class="title">线索录入</h3>
    <div class="box">
      <p class="flex"><Icon class="icon" type="md-alert" /> 使用说明</p>
      <p class="dot">点击下载录入信息模板: <a href="#" @click.prevent="downloadExcel">线索上传模板</a> &nbsp;&nbsp;&nbsp; 注: 每次至多上传1000条;</p>
      <p class="dot">按模板录入用户信息;</p>
      <p class="dot flex">录入完成后上传文件;
        <TalOssUploadUi
          style="margin-left: 10px;"
          v-model="fileList"
          ref="upload"
          :required="true"
          platform="fe-service-platform"
          btnName="上传文件" />
      </p>

      <Button class="btn" type="primary" @click="upload" :loading="uploading" :disabled="fileList.length === 0">提交</Button>
    </div>
  </div>
</template>

<script>
import { upLoadFile } from '@/api/clue'
import config from '@/config'
import { runEnv } from '@/utils/utils'
export default {
  name: 'ClueUpload',
  data () {
    return {
      fileList: [],
      uploading: false,
      uploadTemplatePath: '/excel/线索录入模版.xlsx'
    }
  },
  methods: {
    // 绩效规则下载
    downloadExcel () {
      const env = runEnv()
      window.open(`${config.cdnOss[env]}${this.uploadTemplatePath}`)
    },
    async upload () {
      try {
        this.uploading = true
        const file = this.fileList[0]
        if (file) {
          // 校验文件已经上传完成
          if (file.status !== 5) {
            this.$Message.success({ content: '文件上传中，请稍等' })
          }
          const data = await upLoadFile({ fileUrl: file.path })
          if (data.execResult === '2') {
            this.$Message.error({
              message: '录入失败' + data.messageData,
              duration: 5
            })
          } else {
            this.$Message.success({ content: '录入成功' })
            this.fileList = []
          }
        }
      } catch (error) {
        this.$Message.error({
          content: '录入失败' + error.message,
          duration: 5
        })
      }
      this.uploading = false
    }
  }
}
</script>

